#Header {
  --font-color: #ecf0f1;
  width: 100%;
  background-color: #34495e;
  color: #ecf0f1;
}

#Header .container {
  width: 80%;
  margin: 0 auto;
  padding: 1rem 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

#Header .logo {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
  font-family: 'Amatic SC', cursive;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-width: 250px;
}
#Header .logo__link {
  font-size: 2rem;
}

#Header .user {
  font-family: 'Open Sans', sans-serif;
  font-size: .9rem;
  padding: .5rem 1rem;
  width: 13%;
  min-width: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}
#Header .user__infos{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

#Header .user__infos__username {
  margin: 0 .5rem;
  cursor: default;
}

#Header .user__infos__icon {
  fill: var(--font-color);
}

@media screen and (max-width: 501px) {
  #Header .container {
    width: 100%;
    -ms-flex-pack: end;
        justify-content: flex-end;
  }
}
@media screen and (max-width: 401px) {
  #Header .user {
    padding: .1rem 0;
  }
}



