@import url('https://fonts.googleapis.com/css?family=Amatic+SC|Open+Sans|Roboto');
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body,
html {
  height: 100%;
}

#root {
  min-height: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

.transaction-default{
  width: 100%;  
}
.transaction-default-enter {
  opacity: 0.01;
}

.transaction-default-enter.transaction-default-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.transaction-default-leave {
  opacity: 1;
}

.transaction-default-leave.transaction-default-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}


