#WatchVideo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 70%;
  max-width: 1360px;
  padding: 2rem 0;
  margin: 0 auto;
}

#WatchVideo .main-video {
  width: 70%;
  min-width: calc(360px);
}

@media screen and (max-width: 1360px){
  #WatchVideo {
    width: 100%;
    padding: 2rem;
  }


}
@media screen and (max-width: 678px){
  #WatchVideo .main-video {
    width: 100%;
  }
}



